
@media screen and (max-width: 576px) { /*最大螢幕576：手機*/
.img{
    display:none;
}

}

@media screen and (min-width: 768px) { /*最小螢幕768 平板以上*/
    .Imagemenu{
        padding-top: 8vh;
    }

}




/* 中間圖片 */
.pic{
    margin-left:auto;
    margin:auto;
    margin-bottom: 60px;
    width:85vw;
    height: 110vh;
    z-index: -20;
} 

.Imagemenu{
    padding-bottom: 8vh;
    width: 98.9vw;
    margin: auto;
    display: flex;
    justify-content:center
}

article>img{
    width: 1000px;
    height: 650px;
}