@media screen and (max-width: 768px) { /*最大螢幕576：手機*/
    nav>div:nth-child(1){
        display: none;
    }

    nav>.num{
        font-size: xx-small;
        text-align: center;
    }
    
}



@media screen and (min-width: 992px) { /*最小螢幕992 */
 nav{
    display: flex;
    justify-content: center;
 }
 nav>.num{
     text-align: center;
     padding-right: 20%;
     display: flex;
 }
 nav>.num>div{
     margin: auto;
     padding-right: 10px;
 }
}



/*聯絡方法*/
nav{
    background-color: rgba(165, 150, 150, 0.144);
    width: 100%;
    }
nav>div>img{
    width:10%;
    margin-left: 10px;
}
nav>div:nth-child(1){
    width: 20%;
    text-align: right;
    margin: auto;
}
