@media screen and (max-width: 768px) { 
    .float2>img{
        display: none;
    }
    .float>img{
        width:20px;
        height:200px;
    }
    header>a,.box2{
        font-size: 10px;
        width:15%;
    }
    }

@media screen and (min-width: 992px) { 
    .float2>img{
        width:130px;
        height:130px;
        top:20px;
    }
    .float>img{
        width:58px;
        height:432px;
    }
    header>a,.box2{
        width:10%;
    }

    }



/*上方黑線*/
.box{background-color: rgb(27, 66, 24);height: 10px; ;}

/*選單*/
/* header{
    display:flex;
    justify-content:center ;
    } */
.fix{
    position:fixed;
    top:0;
    left:10px;
    display:flex;
    justify-content:center;
    z-index: 5;
}
.static{
    position:static;
    top:0;
    left:10px;
    display:flex;
    justify-content:center ;
    z-index: 5;

}
header>.panel,#flip
    {
    margin:0px;
    padding:0px;
    text-align:center;
    }
header>.panel
    {
    height:120px;
    display:none;
    padding:0px;
    cursor: pointer
    }
    
    header>a,.box2{
    background-color:white;
    height:50px;
    display:inline-block; /*一行多div*/
    text-align: center; /*水平對齊*/
    line-height: 50px; /*置中*/
    }
.box2>a{width: 100%;height: 100%;}
.box2:hover{background-color:black;color: white;cursor: pointer;}
.box2>img{width: 50px;height: 50px;}


/*右邊*/
.float>img{
position:fixed;right: 10px;top:60px}

/*左上logo*/
.float2>img{
    position:absolute;
    left: 10px;
    }
.float2>img:hover{
    box-shadow:2px 2px 2px rgba(20%,20%,40%,0.6),4px 4px 6px rgba(20%,20%,40%,0.4),6px 6px 12px rgba(20%,20%,40%,0.4);
    cursor: pointer;
}
