@media screen and (max-width: 768px) { /*最大螢幕*/
        /*中間的地圖*/
       .store  article>div>div
        {
            display: none;
    }

    .coffeepic>img{
        width: 100%;
    }

    h2{
        text-align: center;
        margin: 30px 0;
    }
    /* 背景 */
    .img>.pic{
        display: none;
    }

}

@media screen and (min-width: 992px) { 
      /*中間*/
      article>div
      {
      margin:auto;
      margin-bottom: 40px;
      flex-grow: 1;
      height: 500px;
      width: 60vw;
      border-radius: 30px;
      padding: 10px;padding-left: 10px;
      margin-top: 10px;} 

      article>div>h2{padding-left: 80px;}

        /*中間的地圖*/
        article>div>.magnifier
        {width: 400px;
        height: 400px;
        padding-left: 10px;
    }
    h2 
    {padding-left: 80px}

    article>div>.address{
        position:relative ;
        left: 480px;bottom: 350px;
        line-height: 50px;
        width: 25vw;
        height: 35vh;

}
        /*背景*/
        .img>.pic{position: fixed;z-index: -1;margin-bottom: 40px;width:80vw;height: 100vh;margin-left:10vw;opacity: 0.5;}


        .coffeepic{
            display: none;
        }

}
    
        /*地圖旁邊的字*/
        article>div>.address{
            background-color: rgba(253, 252, 252, 0.788);
            padding: 15px;
            text-shadow: rgb(219, 218, 218) 4px 3px 0px;
            text-align: center;
           
        }
        article>div>.address>a{text-decoration:none;color: black;}

        /*門市名字*/
        
           h2 
           {
            color: rgb(255, 255, 255);
            font-size: 50px;
            background-color: rgba(27, 66, 24, 0.808);
            text-shadow: rgb(122, 122, 122) 4px 3px 0px;
            }
        article{display: flex;flex-direction:column;justify-content: space-around;}



