@media screen and (max-width: 576px) { /*最大螢幕：手機*/
    #pic{height: 50%;}
    /*底下小圖片*/
    section{
        width: 80%;
        margin: auto;
        height: 100%;
        overflow: hidden;
        margin-bottom: 20px;

    }
    section>img{
        width: 50%;
        height: 15vh;
        }
    section>img:nth-child(7){
            display: none;
        }
    section>img:nth-child(8){
            display: none;}

}
@media screen and (min-width: 768px) { /*最小螢幕768 平板以上*/
    /*底下小圖片*/
    section{
        width: 100%;
        margin: auto;
        display: flex;
        margin-bottom: 20px;
        justify-content: center;
        overflow: hidden;
        height:20vh;
    }
    
    section>img{
        width: 100%;
        height: 40%;
        display: flex;
        margin: 0 5px;
        overflow: hidden;
        }
}

@media screen and (min-width: 1500px) { 
    section>img{
        height: 80%;}
}


/*中間圖片*/

    .home{
        width: 100vw;
        height: 120%;
    }

    #pic{
    margin-left:auto;
    margin:auto;
    display:block;
    margin-bottom: 40px;
    width:80vw;
} 


    