        
@media screen and (max-width: 768px) {/*最大螢幕576：手機*/

.abc{
    font-size: xx-small;
    margin: 10% 5vh 5vh ;

}
.qrcode>img{
    width:100%
}
.qrcode{
    display: flex;
    align-items: center;
}
.card{
    display: flex;
    margin: auto;
}

}        
        
@media screen and (min-width: 992px) { 
    .abc{
        font-size: 18px;
        margin-bottom: 30vh;
    }
    .qrcode>img{
        width:15vw
    }
    
}
 
        
        
        /*背景*/
        .img>.pic{position: fixed;z-index: -1;margin-bottom: 40px;width:80vw;height: 100vh;margin-left:10vw;opacity: 0.3;}
    
    
    /*加入我們*/
    article{
        width:100vw;
        margin: 0;
    }


        .abc{
            display: grid;
            grid-template-rows: repeat(4);
            align-self: center;

        }
        .d1{
            grid-column: 1 / 3;
            text-align: center;
            border-bottom: rgba(27, 66, 24, 0.808);
            font-size: 30px;
            color: rgb(255, 255, 255);
            background-color: rgba(27, 66, 24, 0.87);
            text-shadow: rgb(122, 122, 122) 4px 3px 0px;
        }
        .d2{
            border-right: cadetblue solid;
        }
        .d3{
            background-color: rgba(180, 190, 176, 0.849);
        }
